import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import {Observable} from 'rxjs/Rx';
import {newField} from './new-field';
import {log} from "util";
import {isUndefined} from "util";

@Component({
  selector: 'app-play-field',
  templateUrl: './play-field.component.html',
  styleUrls: ['./play-field.component.css']
})
export class PlayFieldComponent implements OnInit {

  //tabsPositions = [1,3,5,7,2,4,6,8,1,3,5,7,2,4,6,8];
  currentTab = -1;
  currentIndex = -1;
  selctedTab = -1;
  showTwo = [];
  newGame = new newField(8);
  readyFlag = false;
  tabsPositions = this.newGame.resultsArray;

  @Input() playerId;
  @Input() gameType;

  @Output() stopPlay = new EventEmitter<boolean>();
  backToDashboard(agreed: boolean) {
    this.stopPlay.emit(agreed);
    this.ngOnDestroy();
  };

  @Output() addNewBestTime = new EventEmitter<any>();
  newBestTime(res) {
    this.addNewBestTime.emit(res);

  };

  checkCurrent(index){
    if(this.currentIndex != index){
      this.currentTab = (this.currentTab == index) ? -1 : index;
      let currentValue =  this.tabsPositions[index];
      if(this.selctedTab == currentValue){
        this.showTwo.push(currentValue);
      }else{
        this.selctedTab = currentValue;
      }
      this.currentIndex = index;
    }
    if(this.showTwo.length == 8){
      console.log("Ready");
      this.newBestTime([this.playerId, this.ticks]);
      this.ngOnDestroy();
    }
  }

  ngOnInit(){

  }

  //timer methods and parameters
  ticks = 0;
  sec = 0;
  min = 0;
  private timer;
  private sub;

  startTimer(){
    this.timer = Observable.timer(0,1000);
    console.log(this.playerId, this.gameType);
    this.sub = this.timer.subscribe(t => this.tickerFunc(t));
    this.readyFlag = true;
  };
  tickerFunc(tick){
    this.ticks++;
    if(this.ticks%60 == 0){
      this.sec = 0;
      this.min++;
    }else{
      this.sec++;
    }
  };
  ngOnDestroy(){
    if(this.sub){
      this.sub.unsubscribe();
    }
  }
  //-----------------------------

}


