import { Component } from '@angular/core';
import { Players } from './players';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  play = false;
  title = 'Welcome to "Open Tabs" game!';
  bestPlayers = [
    new Players(0,' Robin', 27, '01:30','pokemon'),
    new Players(1,'Batman', 32, '01:40','pokemon'),
    new Players(2,'Joker', 45, '02:50','pokemon')
  ];
  newHero = [];
  newPlayerIteration = 1;
  newPlayer = new Players(this.newPlayerIteration, 'New Player'+this.newPlayerIteration, 18, 'not results','pokemon');

  currentPlayer = {id:0,game:'pokemon'};

  stopPlay(agreed: boolean) {
    this.play = false;
  };

  addNewPlayer = function(name,age,gameType){
    this.newHero.push(new Players(this.newPlayerIteration,name, age, 'not results',gameType));
    this.newPlayerIteration++;
  };

  addNewBestTime (res) {
    console.log(res);
    let mm1 = Math.floor(res[1]/60),ss1 = res[1]%60;
    let mm = (mm1>9) ? mm1 : (mm1 > 0 && mm1<10) ? '0'+mm1 : '00',
        ss = (ss1 < 10)? '0'+ss1 : ss1;
    this.newHero[res[0]-1].time = mm+' : '+ss;
  };

  toGameField = function (id,game) {
    this.currentPlayer.id = id;
    this.currentPlayer.game = game;
    this.play = true;
  };

  get diagnostic(){return JSON.stringify(this.newPlayer);}
}



